import { useState, useEffect } from 'react';
import { mediaQueryProps } from '../model/mediaQuery';

export const MediaQuery = (props: mediaQueryProps) => {
  const [matches, setMatches] = useState(false);
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const media = window.matchMedia(props.query);
    setMatches(media.matches);

    const handleMediaQueryChange = (event: MediaQueryListEvent) => {
      setMatches(event.matches);
    };
    media.addEventListener('change', handleMediaQueryChange);

    return () => {
      media.removeEventListener('change', handleMediaQueryChange);
    };
  }, [props.query]);

  return matches ? props.children : null;
};
